<template>
    <div>
        <Navbar style="background:#2c3d58;"/>
        <div class="container" style="margin-top:80px;">
            <FormulateForm
            :values="uservalues"
            @submit="handleLogin">
                <div class="row">
                    <div class="col-md-6">
                        <h4>Personal Information</h4>
                        <FormulateInput type="text" name="name" label="Full Name"
                        validation="required"/>
                        <FormulateInput type="text" name="date" label="Date Of Birth"
                        validation="date:MM/DD/YYYY"/>
                        <FormulateInput name="gender" type="radio"
                        label="Gender" validation="required"
                        :options="{male:'male', female:'Female'}"/>
                    </div>

                    <div class="col-md-6">
                        <h4>Contact Information</h4>
                        <FormulateInput type="text" name="phone" label="Phone Number"
                        validation="required|number"
                        />
                        <FormulateInput type="text" name="email" label="Email"
                        validation="required|email"
                        />
                    </div>
                </div>
                <div>
                    <h4>Account Information</h4>
                    <div class="row">
                        <div class="col-md-6">
                            <FormulateInput type="text" name="username"
                            label="Username"
                            validation="required"/>
                            <FormulateInput type="text" name="code"
                            label="Affliate code(Optional)"/>
                        </div>

                        <div class="col-md-6">
                            <FormulateInput type="password" name="password"
                            label="Password"
                            validation="required"/>
                            <FormulateInput type="password"
                            name="password_confirm" label="Confirm Password"
                            validation="required|confirm"/>
                        </div>
                    </div>
                </div>

            <FormulateInput
            type="submit"
            label="Submit"/>

            </FormulateForm>>
        </div>
    </div>
</template>

<script>
import Vue from 'vue'
import VueFormulate from '@braid/vue-formulate'
import Navbar from '../components/Navbar.vue';

Vue.use(VueFormulate)

export default {
    name:'SignUp',
    components:{
        Navbar,
    },
    computed:{
      uservalues(){

      if(localStorage.getItem("email")){
        this.values = {
          email: localStorage.getItem("email"),
          phone: localStorage.getItem("phone")
        }
      }else{
        this.values = JSON.parse(this.$store.getters.signInDetails);
      }



        return this.values;
      }
    },
    created(){
       this.$store.dispatch('get_show_sign_up', true);
    },
    data(){
        return{
            values:{}
        }
    },
    methods:{
        handleLogin(data){
            this.$store.dispatch("store_signup_user_signup_data", data);
        }
    }
}
</script>

<style>
    label{
        color:rgb(219, 200, 200)!important;
    }

    .formulate-input[data-classification="box"]
    .formulate-input-element-decorator::before {
        /* content: ""; */
        display: block;
        background-size: contain;
        background-position: 100% center;
        width: calc(100% - 0.125em);
        height: calc(100% - 0.125em);
        box-sizing: border-box;
        position: absolute;
        top: 0.0625em;
        left: 0.0625em;
    }

    .formulate-input[data-classification="box"] .formulate-input-label--after {
        margin-left: 0.5em;
    }

    .formulate-input .formulate-input-label {
        display: block;
        line-height: 1.5;
        font-size: 0.9em;
        font-weight: 600;
        color: black!important;
        margin-bottom: 0.1em;
    }

    .formulate-input[data-classification="text"] input {
        /* -webkit-appearance: none; */
        font-size: 14px;
        color: rgb(0 0 0)!important;
        border-radius: 0.3em;
        border: 1px solid rgb(206, 206, 206)!important;
        box-sizing: border-box;
        background-color: rgb(0 0 0 / 0%)!important;
        font-size: 0.9em;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
        padding: 0.75em;
        display: block;
        width: 100%;
        font-weight: 400;
        line-height: 1.2em;
        margin: 0px;
    }
.formulate-input[data-classification="button"] button {
    /* -webkit-appearance: none; */
    border-radius: 0.3em;
    box-sizing: border-box;
    font-size: 0.9em;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    padding: 0.75em;
    line-height: 1.2em;
    margin: 0px;
    border: 1px solid rgb(65, 184, 131)!important;
    background-color: rgb(65, 184, 131)!important;
    color: rgb(255, 255, 255)!important;
    min-width: 0px;
    width: auto;
    font-weight: 700;
    /* cursor: pointer; */
    display: inline-flex;
    align-items: center;
}
/* user agent stylesheet
button:disabled {
    color: -internal-light-dark(rgba(16, 16, 16, 0.3), white);
} */

button {
    -webkit-writing-mode: horizontal-tb !important;
    text-rendering: auto;
    color: -internal-light-dark(black, white)!important;
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    text-align: center;
    cursor: default;
    font: 400 13.3333px Arial!important;
}

.formulate-input-element--group {
    display: flex;
    justify-content: space-between;
}

.formulate-input-error{
    color:red!important;
}
.btn{
    margin-top: 50px;
    text-align:center;
}
h4{
    text-align: center;
    color: rgb(0 0 0)!important;
    font-weight: 700;
    margin-top: 80px;
}
</style>
